
import { defineComponent, PropType, ref } from "vue"
import { useRoute, useRouter } from "vue-router"

export default defineComponent({
  props: {
    currentProducts: {
      type: Array as PropType<any[]>,
      default: () => []
    },
    currentIndex: {
      type: Number,
      default: 0
    }
  },
  emits: ["orderShowChange"],
  setup(props, { emit }) {
    const route = useRoute()
    const router = useRouter()
    console.log(route.fullPath)
    const startOrEnd = ref("")
    const distanceToDate = ref("0000000")

    const orderShowChange = () => {
      emit("orderShowChange", true)
    }

    const handleProductDetailClick = () => {
      //  
      if (route.fullPath === "/") {
        router.push(
          `/productdetail/${
            props.currentProducts[props.currentIndex].activityID
          }`
        )
      }
    }
    // /
    const calcTime = () => {
      let curTime = new Date().getTime(),
        startTime = new Date(
          props.currentProducts[props.currentIndex]?.activityStartTime
        ).getTime(),
        endTime = new Date(
          props.currentProducts[props.currentIndex]?.activityEndTime
        ).getTime(),
        date = 0
      if (curTime - startTime < 0) {
        startOrEnd.value = ""
        date = new Date(startTime - curTime).getTime()
      } else {
        startOrEnd.value = ""
        date = new Date(endTime - curTime).getTime()
      }
      let days = Math.floor(date / (24 * 3600 * 1000))
      //
      let leave1 = date % (24 * 3600 * 1000) //
      let hours = Math.floor(leave1 / (3600 * 1000))
      //
      let leave2 = leave1 % (3600 * 1000) //
      let minutes = Math.floor(leave2 / (60 * 1000))
      //
      let leave3 = leave2 % (60 * 1000) //
      let seconds = Math.round(leave3 / 1000)
      distanceToDate.value = `${days}${hours < 10 ? "0" + hours : hours}${
        minutes < 10 ? "0" + minutes : minutes
      }${seconds < 10 ? "0" + seconds : seconds}`
    }
    setInterval(() => {
      calcTime()
    }, 1000)
    return {
      route,
      startOrEnd,
      distanceToDate,
      handleProductDetailClick,
      orderShowChange
    }
  }
})
