// 
export function sendCardByPhone(customerPhoneNumber: string) {
  return yhyRequest.post<any>({
    url: "/sendCard?customerPhoneNumber=" + customerPhoneNumber
  })
}

export function payByCard(
  orderID: string,
  phoneNumber: string,
  customerBankCard: string,
  bankCardPassword: string,
  money: string
) {
  return hyhRequest.post<any>({
    url: "/order/bankPay",
    data: {
      orderID,
      phoneNumber,
      customerBankCard,
      bankCardPassword,
      money
    }
  })
}

// 
export function refineInformation(
  customerPhoneNumber: string,
  customerName: string,
  customerIdType: string,
  customerIdNumber: string,
  customerEmail: string,
  customerAddress: number
) {
  return yhyRequest.post<any>({
    url:
      "/refine?customerPhoneNumber=" +
      customerPhoneNumber +
      "&customerName=" +
      customerName +
      "&customerIdType=" +
      customerIdType +
      "&customerIdNumber=" +
      customerIdNumber +
      "&customerEmail=" +
      customerEmail +
      "&customerAddress=" +
      customerAddress
  })
}

