export function bindCard(
  customerPhoneNumber: string,
  customerBankCard: string,
  code: string
) {
  return yhyRequest.post<any>({
    url:
      "/bind?customerPhoneNumber=" +
      customerPhoneNumber +
      "&customerBankCard=" +
      customerBankCard +
      "&code=" +
      code
  })
}

